import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import GlobalIndex from '@/components/GlobalIndex.vue'
import GlobalSmall from '@/components/GlobalSmall.vue'
import GlobalBig from '@/components/GlobalBig.vue'
Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/global/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/global/index',
      name: 'GlobalIndex',
      component: GlobalIndex
    },
    {
      path: '/global/small',
      name: 'GlobalSmall',
      component: GlobalSmall
    },
    {
      path: '/global/big',
      name: 'GlobalBig',
      component: GlobalBig
    }
  ]
})



