<template>
  <div class="home">
     <div class="btn-fullscreen" @click="handleFullScreen">
           <el-tooltip effect="dark" :content="fullscreen ? `取消全屏`:`全屏`" placement="bottom">
              <i class="el-icon-rank"></i>
           </el-tooltip>
      </div>
      <div class="global-index">
    <div class="container" v-if="data.length">
      <swiper :options="swiperOption[item-1]" ref="mySwiper" class="swiper" v-for="item in 6" :key="item">
    <!-- slides -->
        <swiper-slide v-for="(item,index) in data" :key="index" class='item-container' v-if="data.length">
              <span class="item item-area">
                <i :class="['iconfont',area(item.type)]"></i>
              </span>
              <span class="item item-name">{{ item.name }}</span>
              <span :class="['item','item-nowPri',item.changePri<0?'decrease':'increase']">
                {{item.nowPri}}
              </span>
              <span :class="['item','item-changePri',item.changePri<0?'decrease':'increase']">
                {{item.changePri}}
                <i :class="['iconfont',item.changePri<0?'icon-tubiao04':'icon-tubiao03']"></i>
              </span>
              <span :class="['item','item-increPer',item.increPer<0?'decrease':'increase']">
                {{item.increPer+'%'}}
                <i :class="['iconfont',item.increPer<0?'icon-tubiao04':'icon-tubiao03']"></i>
              </span>
        </swiper-slide>
      </swiper>
    </div>
  <div class='carousel-empty' v-else>{{emptyText}}</div>
  </div>
    
  </div>
</template>

<script>
import { Button , Tooltip } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import 'swiper/dist/css/swiper.css' 
import { swiper, swiperSlide } from 'vue-awesome-swiper'

import axios from 'axios'
import { globaldata } from "@/api";

//字体图标
import '../iconfont/iconfont.css'

export default {
  name: 'GlobalIndex',
  components: {
    "el-button":Button,
     'el-tooltip':Tooltip,
     swiper,
    swiperSlide
  },
  mounted(){
      this.getData()
    setInterval(()=>{
      this.getData()
    },5*60*1000)
  },
  data(){
    return {
      fullscreen: false,
      data:[],
      emptyText:"点击获取数据",
      swiperOption:[{
          loop : true, // 循环模式选项
          speed : 30000,
          initialSlide :0,
          autoplay : { delay: 0 },
          slidesPerView : 2
                },
                {
          loop : true, // 循环模式选项
          speed : 30000,
          initialSlide :2,
          autoplay : { delay: 0 },
          slidesPerView : 2
                },
                {
          loop : true, // 循环模式选项
          speed : 30000,
          initialSlide :4,
          autoplay : { delay: 0 },
          slidesPerView : 2
                },
                {
          loop : true, // 循环模式选项
          speed : 30000,
          initialSlide :6,
          autoplay : { delay: 0 },
          slidesPerView : 2
                },
                {
          loop : true, // 循环模式选项
          speed : 30000,
          initialSlide :8,
          autoplay : { delay: 0 },
          slidesPerView : 2
                },
                {
          loop : true, // 循环模式选项
          speed : 30000,
          initialSlide :10,
          autoplay : { delay: 0 },
          slidesPerView : 2
                }]
      
      }
  },
  methods:{
    // 全屏事件
    handleFullScreen(){
                let element = document.documentElement;
                if (this.fullscreen) {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitCancelFullScreen) {
                        document.webkitCancelFullScreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                } else {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        // IE11
                        element.msRequestFullscreen();
                    }
                }
                this.fullscreen = !this.fullscreen;
                this.data=[];
                this.getData();
    },
    area(where){
      switch(where)
          {
            case "america":
              return "icon-America"
            break;
            case "europe":
              return "icon-nav_europe"
            break;
            case "asia":
              return "icon-Asian"
            break;
            case "australia":
              return "icon-Australia"
            break;
            }

    },
    getData() {
            this.emptyText="数据获取中..."
            return axios.get(globaldata.link).then((res) => {
              console.log(new Date(),res)
                this.data=[];
                this.data = res.data.map((item) => {
                    return {
                        name: item.inxnm,
                        type:item.typeid,
                        nowPri: item.last_price,
                        changePri:Number(item.change_price),
                        increPer: Number(item.change_price_per.split("%")[0])
                    };
                })
              this.emptyText="点击获取数据"      
            }).catch(err=>{
              console.log(new Date(),err)
            })
        }
  }
}
</script>
<style>
.swiper-wrapper{
        transition-timing-function:linear;
    }
</style>
<style scoped>
.global-index{
  font-size:18px;
  font-weight:900;
}
.btn-fullscreen{
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  z-index: 100;
  
}
.btn-fullscreen i{
    color:#cccccc;
}
.swiper{
  /* background:rgba(0,121,243,.2); */
  background:black;
  height: 80px;
  width: 1280px;
  margin:0;
}
.carousel-empty{
  text-align:center;
  height: 480px;
  line-height:480px;
  color:white;
}
.item-container{
  height: 80px;
  line-height:80px;
  display: inline-block;
  text-align:center;
}
.item{
  vertical-align:middle;
  display: inline-block;
  padding-left:20px;
  height: 80px;
}
.item-area i{
  font-size:40px;
  color:rgba(255,255,255,1);
}
.item-name{
  color: white;
  font-family:"Comic Sans MS";
}
.item-nowPri{
  color: white;
}
.increase{
  color:red;
}
.decrease{
  color:#68bb8a;
}
</style>




