export const apiRoot = process.env.API_ROOT

const emuRoot = process.env.API_ROOT

class API {
  constructor(path) {
    this.path = path
  }
  sLink(server) {
    if (server) {
      return server + this.path
    }
    return apiRoot + this.path
  }
  get link() {
    return apiRoot + this.path
  }

  setParam(name, value) {
    if (!this.path) {
      throw new Error("API未初始化")
    }
    let newPath = this.path.replace(":" + name, value)
    return new API(newPath)
  }
}

export const globaldata = new API("/site/globaldata")



