/* eslint-disable */
<template>
    <div class="container">
        <i class="material-icons md-light fullscreen" v-on:click="toggleFullScreen">fullscreen</i>
        <div class="parallelogram" v-for="(item, index) in indexs" :key="index"
            v-bind:style="{
                width: parallelogram.width + 'px',
                height: parallelogram.height + 'px',
                marginTop: parallelogram.marginTop + 'px',
                marginLeft: ( -1 * (index % 3) + 1) * parallelogram.marginLeft + 'px'
            }">
            <div class="base-info">
                <h1 class="name">{{item.name}}</h1>
                <h1 class="value">{{item.nowPri}}</h1>
            </div>
            <div class="price-info" v-bind:class="{ up: parseFloat(item.increPer) > 0, down: parseFloat(item.increPer) < 0 }">
                <h1 class="name"><i class="material-icons md-light md-96 icon">{{ parseFloat(item.increPer) > 0 ? "trending_up" : "trending_down" }}</i></h1>
                <h1 class="value">{{item.increPer + "%"}}</h1>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
import { globaldata } from "@/api";

export default {
    name: 'GlobalIndex',
    data () {
        return {
            parallelogram: {
                width: 0,
                height: 0,
                marginTop: 0,
                marginLeft: 0
            },
            indexs: [],
            data: [],
            page: -2,
            refreshInterval: 15 * 60 * 1000,
            sliceInterval: 10 * 1000
        }
    },
    created() {
        window.addEventListener('resize', this.handleResize)
        this.handleResize()
        this.getData().then(() => {
            this.page = 0
            setInterval(() => {
                this.page = ++this.page * 3 >= this.data.length ? 0 : this.page;
            }, this.sliceInterval)
        })
        setInterval(this.getData, this.refreshInterval)
    },
    destroyed() {
        window.removeEventListener('resize', this.handleResize)
    },
    watch: {
        page: {
            handler: function(val) {
                this.indexs = this.data.slice(this.page * 3, (this.page + 1) * 3)
            }
        }
    },
    methods: {
        handleResize() {
            this.parallelogram.width = window.innerWidth * 0.7
            this.parallelogram.height = window.innerHeight * 0.15
            this.parallelogram.marginTop = window.innerHeight * 0.03
            this.parallelogram.marginLeft = window.innerWidth * 0.032
        },
        toggleFullScreen(e){
            document.fullscreenElement || document.mozFullScreenElement || document.webkitFullscreenElement || document.msFullscreenElement ? document.exitFullscreen ? document.exitFullscreen() : document.msExitFullscreen ? document.msExitFullscreen() : document.mozCancelFullScreen ? document.mozCancelFullScreen() : document.webkitExitFullscreen && document.webkitExitFullscreen() : document.documentElement.requestFullscreen ? document.documentElement.requestFullscreen() : document.documentElement.msRequestFullscreen ? document.documentElement.msRequestFullscreen() : document.documentElement.mozRequestFullScreen ? document.documentElement.mozRequestFullScreen() : document.documentElement.webkitRequestFullscreen && document.documentElement.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT)
        },
        getData() {
            return axios.get(globaldata.link).then((data) => {
                this.data = data.data.map((item) => {
                    return {
                        name: item.inxnm,
                        nowPri: item.last_price,
                        increPer: item.change_price_per.split("%")[0]
                    };
                })
                var length = parseInt(this.data.length / 3) * 3
                this.data = this.data.slice(0, length)
            })
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .fullscreen {
        font-size: 60px;
        color: white;
        position: absolute;
        z-index: 30;
        top: 1em;
        left: 1em;
        cursor: pointer;
    }
    .container {
        display: flex;
        height: 100%;
        flex: 1;
        align-items: center;
        justify-content:center;
        flex-direction: column;
    }
    .parallelogram {
        background: rgba(0, 0, 0, 0.8);
        transform: skew(-10deg);
        display: flex;
    }
    .parallelogram h1 {
        color: white;
        transform: skew(10deg);
    }
    .parallelogram .base-info {
        display: inline-block;
        width: 58%;
        display: flex;
        align-items: center;
        flex: 75;
    }
    .parallelogram .price-info {
        float: right;
        height: 100%;
        width: 38%;
        display: flex;
        align-items: center;
        flex: 35;
    }
    .parallelogram .price-info.up { background-color: rgba(255, 0, 0, 0.5); }
    .parallelogram .price-info.down { background-color: rgba(0, 255, 0, 0.5); }
    .parallelogram .name {
        margin-left: 1em;
        flex: 1;
    }
    .parallelogram .value { margin-right: 1.5em; }
    .parallelogram .icon { color: white; font-size: 48px; flex: 1; margin-top: 0.3em; }
</style>



