<template>
  <div class="home">
     <div class="btn-fullscreen" @click="handleFullScreen">
           <el-tooltip effect="dark" :content="fullscreen ? `取消全屏`:`全屏`" placement="bottom">
              <i class="el-icon-rank"></i>
           </el-tooltip>
      </div>
      <div class="global-index">
    <div class="container" v-if="data.length">
      <div v-for="thisitem in 2" :key="thisitem">
        <swiper :options="swiperOption[thisitem-1]" ref="mySwiper" class="swiper" v-if="type">
        <!-- slides -->
          <swiper-slide v-for="(items,index) in data" :key="index" class='item-container'>
              <div v-for="(item,index1) in items" :key="index1" class='items'>
                  <span class="item item-area">
                  <i :class="['iconfont',area(item.type)]"></i>
                </span>
                <span class="item item-name">{{ item.name }}</span>
                <span :class="['item','item-nowPri',item.changePri<0?'decrease':'increase']">
                  {{item.nowPri}}
                </span>
                <span :class="['item','item-changePri',item.changePri<0?'decrease':'increase']">
                  {{item.changePri}}
                  <i :class="['iconfont',item.changePri<0?'icon-tubiao04':'icon-tubiao03']"></i>
                </span>
                <span :class="['item','item-increPer',item.increPer<0?'decrease':'increase']">
                  {{item.increPer+'%'}}
                  <i :class="['iconfont',item.increPer<0?'icon-tubiao04':'icon-tubiao03']"></i>
                </span>
              </div>
          </swiper-slide>
        </swiper>
        <div class="swiper text" :class="'swiper'+thisitem" v-else>
            <div v-for="index in text[thisitem-1].length" :key="index">
                {{text[thisitem-1][index-1]}}
            </div>
        </div>
      </div>
      
    </div>
  <div class='carousel-empty' v-else>{{emptyText}}</div>
  </div>
  <div class="change">
    <el-switch
      style="display:inline-block"
      v-model="type"
      active-color="#13ce66"
      inactive-color="#ff4949"
      active-text="股指模式"
      inactive-text="文字模式">
    </el-switch>
  <div class="change-input" v-if="!type">
      <el-input v-for="(value,index) in text" v-model="text[index]" :key="index" placeholder="请输入内容"></el-input>
    </div>
  </div>
    
  </div>
</template>

<script>
import { Button , Tooltip , Switch, Input} from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

import 'swiper/dist/css/swiper.css' 
import { swiper, swiperSlide } from 'vue-awesome-swiper'

import axios from 'axios'
import { globaldata } from "@/api";

//字体图标
import '../iconfont/iconfont.css'

export default {
  name: 'GlobalSmall',
  components: {
    "el-button":Button,
     'el-tooltip':Tooltip,
     'el-switch':Switch,
     'el-input':Input,
     swiper,
    swiperSlide
  },
  mounted(){
      this.getData()
    setInterval(()=>{
      this.getData()
    },5*60*1000)
  },
  data(){
    return {
      fullscreen: false,
      data:[],
      //类型：文字或者轮播
      type:true,
      text:[
        "贵州友山教育考试中心",
        "预祝各位考生考试成功！"
      ],
      emptyText:"刷新获取数据",
      swiperOption:[{
          loop : true, // 循环模式选项
          initialSlide :0,
          speed : 30000,
          autoplay : { delay: 0 },
          slidesPerView : 2
                },
                {
          loop : true, // 循环模式选项
          initialSlide :2,
          speed : 30000,
          autoplay : { delay: 0 },
          slidesPerView : 2
                }]
      
      }
  },
  methods:{
    // 全屏事件
    handleFullScreen(){
                let element = document.documentElement;
                if (this.fullscreen) {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitCancelFullScreen) {
                        document.webkitCancelFullScreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                } else {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        // IE11
                        element.msRequestFullscreen();
                    }
                }
                this.fullscreen = !this.fullscreen;
                this.data=[];
                this.getData();
    },
    area(where){
      switch(where)
          {
            case "america":
              return "icon-America"
            break;
            case "europe":
              return "icon-nav_europe"
            break;
            case "asia":
              return "icon-Asian"
            break;
            case "australia":
              return "icon-Australia"
            break;
            }

    },
    getData() {
            this.emptyText="数据获取中..."
            return axios.get(globaldata.link).then((res) => {
                console.log(new Date(),res)
                this.data=[];
                this.data = res.data.map((item) => {
                    return {
                        name: item.inxnm,
                        type:item.typeid,
                        nowPri: item.last_price,
                        changePri:Number(item.change_price),
                        increPer: Number(item.change_price_per.split("%")[0])
                    };
                })
                let newArr=[];
                for(var i=0;i<8;i++){
                    newArr.push(this.data.splice(0,2))
                }
                this.data=newArr;
              this.emptyText="刷新获取数据"      
            }).catch(err=>{
              console.log(new Date(),err)
            })
        }
  }
}
</script>
<style>
.swiper-wrapper{
        transition-timing-function:linear;
    }
</style>
<style scoped>
.global-index{
  font-size:24px;
  font-weight:900;
}
.btn-fullscreen{
  display:none;
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 30px;
  z-index: 100;
  
}
.btn-fullscreen i{
    color:#cccccc;
}
.swiper{
  /* background:rgba(0,121,243,.2); */
  background:black;
  height: 160px;
  width: 1380px;
  margin:0;
}
.carousel-empty{
  text-align:center;
  height: 480px;
  line-height:480px;
  color:white;
}
.item-container{
  height: 160px;
  line-height:80px;
  display: inline-block;
  /* text-align:center; */
}
.items{
    width: 100%;
    height: 80px;
    display: inline-block;
    float: left;
}
.item{
  width: 135px;
  vertical-align:middle;
  display: inline-block;
  padding-left:5px;
  height: 80px;
  overflow: hidden;
  float: left;
}
.item-area{
    width: 50px;
    overflow:visible;
}
.item-area i{
  font-size:50px;
  color:rgba(255,255,255,1);
}
.item-name{
  color: white;
  width: 200px;
  font-family:"Comic Sans MS";
}
.item-nowPri{
  color: white;
}
.increase{
  color:red;
}
.decrease{
  color:#68bb8a;
}
.text{
  color:red;
  text-align: center;
  line-height: 162px;
  font-size: 120px;
  display: flex;
  justify-content:space-around;
}
.change{
  margin: 40px auto 0;
  text-align: center
}
.change-input{
  width: 50%;
  margin: 10px auto;
}
.change-input>div{
  margin-bottom: 5px;
}
</style>




